
.SideNav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #0077c5;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 6%;
  }
  
  .SideNav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 180%;
    color: #ffffff;
    display: block;
    transition: 0.3s;
  }
  
  .SideNav a:hover {
    color: #8d9096;
  }
  
  .SideNav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 50px;
    margin-left: 50px;
  }
  
  span{
    font-size: 700%;
  }
  
  
  #main {
    transition: margin-left 0.5s;
    padding: 1%;
  }

  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }
  

  